<template>
  <div>
    <el-card>
      <el-row>
        <el-col
          :span="4"
          v-for="(item, index) in bookList"
          :key="item.id"
          :offset="index > 0 ? 1 : 0"
          style="padding-top: 12px"
        >
          <el-card :body-style="{ padding: '4px' }" shadow="hover">
            <!-- style="width: 140px; height: 180px" -->
            <img
              :src="item.img"
              class="image"
              @click="$router.push(`/detaillogin/${item.ID}`)"
            />
            <div style="padding: 12px">
              <div id="Bfenlei">{{ item.category.name }}ï¼</div>
              <div id="Bshumingzuozhe">
                <span id="Bshuming">ã{{ item.title }}ã</span>
                <span id="Bzuozhe">--{{ item.author }}</span>
              </div>
              <div id="Bdesc">
                <span style="color: rgb(122, 224, 144)">ç®ä»ï¼</span
                >{{ item.Desc }}
              </div>
              <!-- <div id="Bdesc">
                <el-button class="Btopbutton" type="text" @click="open"
                  >å¾ä¹¦ç®ä»</el-button
                >
              </div> -->
              <div id="Btime">
                <i class="el-icon-date"></i
                >{{ item.CreatedAt | dateformat("YYYY-MM-DD HH:ss") }}
              </div>
              <el-divider class="Bxian"></el-divider>
              <div class="bottom clearfix">
                <span id="Bprice">ä»·æ ¼ï¼{{ item.price }}</span>
                <el-button
                  type="text"
                  class="button Btopbutton"
                  :id="item.ID"
                  @click="add($event)"
                  ><i class="el-icon-shopping-cart-1">æ·»å </i></el-button
                >
              </div>
            </div>
          </el-card>
        </el-col>
      </el-row>
      <div id="Bfenye">
        <el-pagination
          v-model="queryParam.pagenum"
          layout="prev, pager, next"
          background
          :page-size="queryParam.pagesize"
          :total="this.total"
          :current-page.sync="queryParam.pagenum"
          @current-change="getCateBook(id)"
        >
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
import { Url } from "../../plugin/http";
export default {
  props: ["id"],
  data() {
    return {
      upUrl: Url + "/upload",
      headers: {}, // è¯·æ±å¤´
      currentDate: new Date(),
      bookList: [],
      queryParam: {
        // è¯·æ±
        pagesize: 5,
        pagenum: 1,
      },
      total: 0,
    };
  },
  created() {
    // this.getBookList();
    this.getCateBook(this.id);
    this.headers = {
      Authorization: `Bearer ${window.sessionStorage.getItem("token")}`,
      username: window.sessionStorage.getItem("username"),
      id: window.sessionStorage.getItem("user_id"),
    };
  },
  methods: {
    // è·ååç±»ä¸çå¾ä¹¦
    async getCateBook(id) {
      const { data: res } = await this.$http.get(`/book/list/${id}`, {
        params: {
          pagesize: this.queryParam.pagesize,
          pagenum: this.queryParam.pagenum,
          // id: this.queryParam.id,
        },
      });
      this.bookList = res.data;
      this.total = res.total;
      //console.log(this.bookList);
      //console.log(this.total);
    },

    // æ·»å 
    add(e) {
      // console.log(e.currentTarget.id)
      // alert(this.headers.id)
      var bools = confirm("æ¯å¦æ·»å è¯¥å¾ä¹¦?");
      if (bools == false) {
        return;
      }
      var bid = parseInt(e.currentTarget.id);
      this.$http.post("cart/add", {
        uid: parseInt(this.headers.id),
        bid: bid,
      });
      location.reload();
    },
  },
};
</script>

<style>
.Btopbutton {
  padding: 0 !important;
}
#Bprice {
  font-size: 15px;
  color: rgb(230, 64, 64);
}
#Btime {
  font-size: 10px;
  text-align: center;
}
#Bdesc {
  height: 60px;
  font-size: 12px;
  padding-top: 4px;
  margin-bottom: 4px;
  color: rgb(106, 118, 122);
}
#Bshumingzuozhe {
  width: auto;
  height: 40px;
}
#Bzuozhe {
  font-size: 12px;
  color: rgb(202, 105, 105);
}
#Bshuming {
  font-size: 14px;
  color: rgb(0, 209, 209);
}
#Bfenlei {
  font-size: 11px;
  color: rgb(201, 201, 201);
}
#Bfenye {
  margin-top: 20px;
  text-align: center;
}
.Bxian {
  margin: 5px 0 !important;
}
.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  display: block;
  cursor: pointer;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both;
}
</style>