<script>
export default {
  data() {
    return {
      currentDate: new Date(),
      bookList: [],
      queryParam: {
        // è¯·æ±
        pagesize: 5,
        pagenum: 1,
        title: "",
      },
      total: 0,
      id: 1,
    };
  },
  created() {
    this.getBookList();
    //this.getCateBook(this.id)
  },
  methods: {
    // è·åå¾ä¹¦åè¡¨
    async getBookList() {
      const { data: res } = await this.$http.get("book", {
        params: {
          pagesize: this.queryParam.pagesize,
          pagenum: this.queryParam.pagenum,
          title: this.queryParam.title,
        },
      });
      this.bookList = res.data;
      this.total = res.total;
      // console.log(this.bookList);
      // console.log(this.total);
      // console.log(this.queryParam.pagesize,this.queryParam.pagenum);
    },
  },
};
</script>

